import React from 'react';

const CustomIcon = React.memo(({   name, width = 60, height = 60, fontSize, style = {} }) => {
    // const styles = { width, height, verticalAlign: 'middle' }
    const styles = { ...style, width, height, verticalAlign: 'middle' }

    if (name === 'spring') {
        return (
            <img style={styles} src="/img/icon/spring-logo.png" />
        );
    } else if (name === 'node') {
        return (
            <img style={styles} src="/img/icon/nodejs.png" />
        );
    } else if (name === 'php') {
        return (
            <img style={styles} src="/img/icon/php-logo.png" />
        );
    } else if (name === 'aws') {
        return (
            <img style={styles} src="/img/icon/amazon-web-services.png" />
        );
    } else if (name === 'mysql') {
        return (
            <img style={styles} className="ch" src="/img/icon//mysql-logo.png" />
        );
    }
    else if (name === 'mongodb') {
        return (
            <img style={styles} src="/img/icon/mongodb.png" />
        );
    }
    else if (name === 'nginx') {
        return (
            <img style={styles} src="/img/icon/nginx.png" />
        );
    }
    else if (name === 'linux') {
        return (
            <img style={styles} src="/img/icon/linux.png" />
        );
    }
    else if (name === 'git') {
        return (
            <img style={styles} src="/img/icon/git.png" />
        );
    }
    else if (name === 'github') {
        return (
            <img style={styles} src="/img/icon/github.png" />
        );
    } else if (name === 'express') {
        return (
            <h4 style={{...styles, fontSize: `${fontSize}rem`}} >Express</h4>
        );
    } else if (name === 'xcode') {
        return (
            <img style={styles} src="/img/icon/xcode.png" />
        );
    } else if (name === 'ios') {
        return (
            <img style={styles} className="ch" src="/img/icon/ios-logo.png" />
        );
    } else if (name === 'swift') {
        return (
            <img style={styles} src="/img/icon/swift.png" />
        );
    } else if (name === 'angular') {
        return (
            <img style={styles} src="/img/icon/angularjs.png" />
        );
    } else if (name === 'react') {
        return (
            <img style={styles} src="/img/icon/react-native.png" />
        );
    } 
    else if (name === 'unity') {
        return (
            <img style={styles} className="ch" src="/img/icon/unity.png" />
        );
    }
    else if (name === 'illustrator') {
        return (
            <img src="/img/icon/adobe-illustrator.png" />
        );
    }
    else if (name === 'photoshop') {
        return (
            <img src="/img/icon/adobe-photoshop.png" />
        );
    }
    else if (name === 'xd') {
        return (
            <img src="/img/icon/adobe-xd.png" />
        );
    }
    else if (name === 'after-effects') {
        return (
            <img src="/img/icon/adobe-after-effects.png" />
        );
    }
    else if (name === '3ds-max') {
        return (
            <img src="/img/icon/autodesk-3ds-max.png" />
        );
    }
}
);

export default CustomIcon;