import styled from 'styled-components';
import React, { useRef, useLayoutEffect, useState } from 'react'
import { section1, section2, section3 } from '../../projectList';
import ServerHead from "./Details/ServerHead";
import EarlyBuddy from "./Details/EarlyBuddy";
import Tooc from "./Details/Tooc";
import SoptPage from "./Details/SoptPage";
import GetRest from "./Details/GetRest";
import '../../scss/post.scss';
import { Link } from 'react-router-dom';

const DetailContainer = styled.div`
    width: 100%;
    height: 100vh;
    top: 0px;
    left: 0px;
    position: fixed;
    overflow: auto;
    background-color: #fff;
    z-index: 11;
`

const ImageBack = styled.div`
    // position: fixed;
    width: 100%;
    height: 100vh;
    z-index: 111;
    background-image: ${props => 'url(' + props.image + ')'};
    background-position: center;
    background-size: cover;
    display: inline-block;
`;

const ProjectDetail = React.memo(({location}) => {
    const dataList = section1.concat(section2, section3);
    let content = <></>;
    const link = location.pathname.split('/')[2];
    const data = dataList.find(d => d.link === link);
    // const titleRef = useRef(null);
    const [opacity, setOpacity] = useState(1);

    if (link === 'server-head')
            content = <ServerHead key={link}/>
    else if (link === 'early-buddy')
        content = <EarlyBuddy key={link}/>
    else if (link === 'tooc')
        content = <Tooc key={link}/>
    else if (link === 'sopt-page')
        content = <SoptPage key={link}/>
    else if (link === 'get-rest')
        content = <GetRest key={link} />
    
    // useLayoutEffect(() => {
    //     const topPos = element => element.getBoundingClientRect().top;
    //     const makeDecimalFraction = num => {
    //         while (num > 1) num /= 10;
    //         return num;
    //     }
    //     const initPos = window.scrollY + window.innerHeight;
    //     const init = makeDecimalFraction(initPos);

    //     const onScroll = () => {
    //         const scrollPos = window.scrollY + window.innerHeight;
    //         const scroll = makeDecimalFraction(scrollPos);
    //         const disappear = opacity - (scroll - init);
    //         // console.log('disappear : ', disappear);
    //         setOpacity(disappear);
    //     };



    //     window.addEventListener("scroll", onScroll);
    //     return () => window.removeEventListener("scroll", onScroll);
    // }, []);

    return (
        <div className="project-detail">
            {
                data ?
                        <DetailContainer>
                        <div className="full-screen-square"/>
                            <Link to='/project'>
                                <img id="back-btn" className="back-btn link" src="/img/cancel-red.png" />
                            </Link>
                            <div className="post">
                                <ImageBack image={data.backImage} className="proj-title" style={{opacity: opacity}}>
                                    <div className="title">
                                        <h2> {data.duration} </h2>
                                        <h3> {data.title}</h3>
                                    </div>
                                </ImageBack>
                                <div style={{height: '600px'}}></div>
                                <div className="content">
                                    {content}
                                </div>
                            </div>
                        </DetailContainer>
                    :
                    <></>
            }
        </div>
    )
});

export default ProjectDetail;